import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Table, Button, Modal } from 'react-bootstrap';
import { connect, useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';


let 수량변경버튼 = styled.button`
box-sizing: border-box;
font-size: 25px;
`
let 상품삭제버튼 = styled.button`
    margin-left: 15px;
`


function Cart(props) {

    let state = useSelector((state)=> state );
    
    let history = useHistory();

    let dispatch = useDispatch();

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    let [총가격, 총가격변경] = useState(state.reducer.reduce(function(prev, next){
        return prev + next.price * next.quan;
    }, 0));
 
    return (
        <div>
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>상품명</th>
                        <th>수량</th>
                        <th>가격</th>
                        <th>변경</th>

                    </tr>
                </thead>
                <tbody>
                    {
                        state.reducer.map((상품하나, i) => {
                            return (
                                <tr key={i}>
                                    <td>{i + 1}</td>
                                    <td>{상품하나.name}</td>
                                    <td>{상품하나.quan}</td>
                                    <td>{상품하나.price * 상품하나.quan}원</td>
                                    <td><수량변경버튼 onClick={() => {
                                        dispatch({
                                            type: '수량증가',
                                            payload: { idx: i }
                                        });
                                        총가격변경(총가격+상품하나.price);
                                    }}>+</수량변경버튼>
                                        <수량변경버튼 onClick={() => {
                                            if(상품하나.quan > 1){
                                            dispatch({
                                                type: '수량감소',
                                                payload: { idx: i }
                                            });
                                            총가격변경(총가격-상품하나.price);}
                                        }}>-</수량변경버튼>
                                        <상품삭제버튼 onClick={() => {
                                            dispatch({
                                                type: '상품삭제',
                                                payload: { idx: i }
                                            });
                                            총가격변경(총가격-상품하나.price*상품하나.quan);
                                        }}>삭제</상품삭제버튼></td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </Table>
            {
                props.alertstate === true ?
                    <div className="my-alert2">
                        <p>지금 구매하시면 신규할인 20%</p>
                        <button onClick={() => { dispatch({ type: 'alert닫기' }) }}>닫기</button>
                    </div>
                    : null
            }

            <div>
                <Button variant="warning" onClick={handleShow}>
                    결제하기
                </Button>

                <Modal
                    show={show}
                    onHide={handleClose}
                    backdrop="static"
                    keyboard={false}
                >
                    <Modal.Header closeButton>
                        <Modal.Title>주문 완료</Modal.Title>
                    </Modal.Header>
                    <Modal.Body> 
                    <p>총 { 총가격 }원을 다음 계좌로 입금해주세요</p>
                    <p>카카오뱅크 3333144441234 이승주 </p>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="primary" onClick={()=>{handleClose(); 
                            dispatch({
                                type: '장바구니초기화'
                            });
                            총가격변경(0);
                        }}>
                            주문한 상품 지우기
                        </Button>
                        <Button variant="secondary" onClick={handleClose}>
                            닫기
                        </Button>
                    </Modal.Footer>
                </Modal>
            </div>
        </div>
    )
}


// function state를props화(state) {
//     return {
//         state: state.reducer,
//         alertstate: state.reducer2
//     }
// }

// export default connect(state를props화)(Cart)

export default Cart;