import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';

import { BrowserRouter } from 'react-router-dom';

import { Provider } from 'react-redux';
import { combineReducers, createStore } from 'redux';
import { computeHeadingLevel } from '@testing-library/react';

let 초기값 = []

function reducer(state = 초기값, 액션) {   //데이터 수정하는 방법을 담은 함수
  if (액션.type === '수량증가') {
    let copy = [...state];
    copy[액션.payload.idx].quan++;
    return copy
  }
  else if(액션.type === '수량감소'){
    let copy = [...state];
    copy[액션.payload.idx].quan--;
    copy[액션.payload.idx].quan = copy[액션.payload.idx].quan;
    return copy;
  }
  else if(액션.type === '상품삭제'){
    let copy = [...state];
    copy.splice(액션.payload.idx, 1);
    return copy;
  }
  else if(액션.type === '항목추가'){
    let copy = [...state];
    let tmp상품 = {id: 액션.payload.id, name: 액션.payload.name, quan: 액션.payload.quan, price: 액션.payload.price};
    let 상품idx = copy.findIndex(상품=>상품.id == tmp상품.id);
    상품idx === -1? copy.push(tmp상품) : copy[상품idx].quan++;
    return copy;
  }
  else if(액션.type === '장바구니초기화'){
    return [];
  }
  else { 
    return state 
  }      //항상 state변수를 뱉어야함
}

let alert초기값 = true;
function reducer2(state = alert초기값, 액션){
  if( 액션.type ==='alert열기'){
    return true;
  }
  else if(액션.type === 'alert닫기'){
    return false;
  }
  else{
    return state;
  }
}


let store = createStore(combineReducers({reducer, reducer2}));

localStorage.setItem('최근본상품', JSON.stringify([]));

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

reportWebVitals();
